<template>
  <div >
    <div class="mhead" v-if="!hideHead">
      <ul>
        <li>
          <text>购气写卡</text>
        </li>
      </ul>
    </div>
    <div class="body">
      <div class="signal">
        <div class="picsin">
          <div class="picview">
            <img :src="imgArray[index]"/>
          </div>
        </div>
        <div class="textsin">
          <dl>
            <dt>
              <span>{{message}}</span>
              <br/>
              <span v-if="isdone && isfail && !printFail && failCount<writeRetryTime" style="color:red;">{{msg}}</span>
            </dt>
          </dl>
        </div>
      </div>
      <div class="btnpanel">
        <dl class="sucbtns" v-if="isdone && printFail">
          <dd>
            <button type="button" class="btn btn-lg btn-info" @click="toprint()">重新打印</button>
          </dd>
        </dl>
        <dl class="sucbtns" v-if="isdone && isfail && !printFail && failCount<writeRetryTime">
          <dd>
            <button type="button" class="btn btn-lg btn-info" @click="writecard()">重新写卡</button>
          </dd>
        </dl>
        <dl class="sucbtns" v-if="isdone && isbackerro">
          <dd>
            <button type="button" class="btn btn-lg btn-info" @click="writecard()" >重新保存</button>
          </dd>
        </dl>
      </div>
    </div>
    <div class="foot" style="margin-top: -10%;" v-if="checkResult">
      <text>请等待自动写卡</text>
    </div>
    <div class="foot" v-if="!checkResult" @click="checkUser()" style="margin-top: -10%;">
      <text >退出</text>
    </div>
    <advert-pop  :show = "show" :advertpath="advertPath" :timeout="countDown"  :msg="message" ></advert-pop>
  </div>
</template>
<script>
import co from 'co'
import Vue from 'vue'


//todo
let TimeOutObj
let InterValObj

let timeoutAutoBack = function *(self) {
  if(Vue.PosUtil.posParam.AutoBack){
    TimeOutObj = window.setInterval(() => {
      self.back('tomain')
    },  Vue.PosUtil.posParam.AutoBackTime)
  }else {
    console.log('写卡失败等待重写写卡，不自动退出')
  }
}

let writeBack =function * (self){
  console.log("写卡完成，开始保存写卡结果",self)
  self.backData.f_write_card = self.isWrited
  self.message = '正在保存写卡结果'
  return  self.$resetpost(Vue.ProxyUrl + '/rs/logic/'+self.$PosUtil.posParam.revenue+'writeBack', {data: self.backData},
    {resolveMsg: null, rejectMsg: null,aoteEncrypt:Vue.PosUtil.posParam.EncryType,encryKey: Vue.PosUtil.posParam.EnceyKey})
}

let writeBackGen = function * (self){
  let  writebackret
  try {
    writebackret = yield co(writeBack(self))
    console.log("写卡结果返回writebackret",writebackret)
    if(!writebackret) {
      self.message = '保存写卡结果失败，请检查网络连接正常再点击重新保存按钮'
      self.isbackerro = true
      return
    }
    console.log("保存写卡结果成功")
    self.showAdvert ()
  }catch (error){
    console.log(error)
    if(!writebackret){
      self.message = '保存写卡结果失败，请检查网络连接正常再点击重新保存按钮'
      self.isbackerro = true
    }
  }
}

let printGen  = function * (self) {
  self.printFail = false
  self.checkResult = false
  self.message = '正在打印凭据，请稍等！'
  let pres =  self.print()
  console.log('打印',pres)
  if (pres.code === 200) {
    console.log('打印成功',pres)
    self.index = 0
    self.message = '打印成功！稍候返回首页！！'
    // self.back('tomain')
    window.setTimeout(function () {
      self.back('tomain')},5000)
  } else {
    console.log('打印失败',pres)
    self.index = 2
    self.message = '打印异常！请确保小票打印纸正常(无缺纸或无卡纸现象)和POS机电量充足（电量不足可直接插上电源）后，点击“重新打印”！'
    self.printFail = true
    return
  }
}

let getParamGen = function * (self) {
  let condition = ''
  if (self.$PosUtil.posParam.filiale.hasFiliale) {
    condition = self.$PosUtil.posParam.filiale.f_filialeid
  }
  return self.$resetpost(Vue.ProxyUrl + '/rs/logic/'+self.$PosUtil.posParam.revenue+'stairCard', {data: {f_card_id: self.cardInfo.CardID, f_alias: self.cardInfo.Factory,id:self.backData.id, f_filialeid:condition}
  }, {resolveMsg: null, rejectMsg: '获取写卡参数失败',aoteEncrypt:Vue.PosUtil.posParam.EncryType,encryKey: Vue.PosUtil.posParam.EnceyKey})
}


// 卡表收费
let sellGasGen = function * (self) {

  self.message = '开始写卡，请稍候...'
  self.isdone = false
  self.isfail = false
  let cardParams
  let writeRes
  self.index = 0
  try {
    cardParams =yield co(getParamGen(self))
    console.log('写卡参数获取结果',cardParams)
    if (!cardParams) {
      self.index = 1
      self.message = '获取写卡参数失败，请重新写卡或联系工作人员！'
      self.isWrited = '写卡失败'
      self.isdone = true
      self.isfail = true
      if (self.failCount >= self.writeRetryTime) {
        co(writeBackGen(self))
      }else {
        co(timeoutAutoBack(self))
      }
      return
    }
    let params = self.$PosUtil.writeCardParams(cardParams, self)
    console.log('写卡参数',params)
    self.message = '正在校验卡号...'
    let readinfo = self.$AndroidPlugin.read()
    console.log('卡信息',self.cardInfo)
    console.log('读的信息',readinfo)
    // self.cardInfo.CardID = 1
    if (readinfo.code !== 200 || readinfo.msg.CardID !== self.cardInfo.CardID) {
      self.index = 1
      self.message = '卡号校验失败,请核对用户信息,使用正确的燃气卡重新写卡'
      self.isWrited = '写卡失败'

      self.isdone = true
      self.isfail = false
      if (self.failCount >= self.writeRetryTime) {
        co(writeBackGen(self))
      }else {
        co(timeoutAutoBack(self))
      }
      return
    }
    // console.log('卡号校验成功，开始写卡')
    self.message = '卡号校验成功！正在写卡...'
    writeRes = self.$AndroidPlugin.sell(params)
    // console.log('写卡结果writeRes==',writeRes)
    self.backData = Object.assign({}, writeRes.msg, self.backData)
    console.log('backData',self.backData)
    if (writeRes.code === 200) {
      self.message = '写卡完成，正在更新写卡状态...'
      self.isWrited = '已写卡'
      self.isdone = true
      self.isfail = false
      co(writeBackGen(self))
      return
    } else {
      self.index = 1
      var errorMsg = writeRes.msg ? (!writeRes.msg.Err ? writeRes.msg.Exception : writeRes.msg.Err) : '写卡未知错误！'
      self.message = errorMsg + ' 写卡失败！请重新写卡或联系工作人员！'
      self.isWrited = '写卡失败'
      self.isdone = true
      self.isfail = true
      if (self.failCount >= self.writeRetryTime) {
        co(writeBackGen(self))
      }else {
        co(timeoutAutoBack(self))
      }
      return
    }
  } catch (error) {
    self.index = 1
    self.message = '写卡异常!请重新写卡或联系工作人员！'
    self.isWrited = '写卡失败'
    self.isdone = true
    self.isfail = true
    if (self.failCount >= self.writeRetryTime){
      co(writeBackGen(self))
    }else {
      co(timeoutAutoBack(self))
    }
  }
}

export default {
  title: '收费写卡',
  data () {
    return {
      index: 0,
      checkResult: true,
      isbackerro:false,
      isWrited: '未写卡',
      isRefund: '否',
      isfail: false,
      writeRetryTime: 0,
      failCount: 0,
      Advert: {
        hasAdvert: false,
        imgName:"",
        outTime: 0
      },
      countDown: 0,
      advertPath: "",
      isdone: false,
      printFail:false,
      advertName: "",
      message: '正在初始化，请等待...',
      msg: '请重新插牢燃气卡片，确保芯片面朝上，确认后，点击“重新写卡”按钮！',
      backData:{},
      // info: '多次写卡失败，禁用写卡，请前往营业厅处理,带来不便敬请谅解!',
      imgArray: [
        // require('../../image/loading.gif'),
        require('../../image/success.png'),
        require('../../image/fail.png'),
        require('../../image/fail2.png')
      ],
      show: false

    }
  },
  props: ['userInfo', 'cardInfo', 'stairPrice','hideHead'],
  methods: {
      tomain () {
          this.$back('tomain')
          // HostApp.backHome()
      },
      checkUser () {
        this.$goto('init',{},'self', this.back)
      },
      back (str) {
        window.clearInterval(InterValObj) // 停止计时器
        this.show = false
        that.$goto('main')
      },
     isprint() {
       co(timeoutAutoBack(this))
       const that = this
       this.$showMessage('退出前是否需要打印小票，点击"确认"打印，"取消"不打印', ['confirm', 'cancel']).then((res) => {
         if (res === 'confirm') {
           that.doprint()
         }else{
           that.$goto('main')
         }
       })
     },
    writecard () {
      this.failCount += 1
      if(this.TimeOutObj){
        window.clearInterval(this.TimeOutObj)
      }
      let sellgas = sellGasGen(this)
      return co(sellgas)
    },
    toPrint(){
      if(!Vue.PosUtil.posParam.autoPrint){
        this.isprint()
      }else {
        this.doprint()
      }
    },
    showAdvert () {
      console.log('判断是否显示广告',this.Advert && this.Advert.hasAdvert)
      if(this.Advert && this.Advert.hasAdvert){
        if(!this.isfail){
          this.message = '写卡成功'
        }else {
          this.message = '写卡失败'
        }
        console.log('显示广告并启动倒计时定时器')
        this.show = true
        InterValObj = window.setInterval(this.outOfPage, 1000)
      }else {
        console.log('不显示广告')
        this.toPrint()
      }
    },
    doprint(){
      let reprints = printGen(this)
      return co(reprints)
    },

    print () {
      // 清空打印内容
      console.log("this.stairPrice", this.stairPrice)
      var printcontent = []
      // 抬头
      var comName = this.$PosUtil.posParam.companyName

      printcontent.push({type: 3, text: comName+'购气凭证', fontsize: 3, isbold: true, align: 'center'
      })
      printcontent.push({type: 3, text: '----------------------------', fontsize: 3, isbold: true, align: 'center'})
      printcontent.push({type: 3, text: '用户编号：' + this.userInfo.f_userinfo_code, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '用户姓名：' + this.userInfo.f_user_name, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '用户地址：' + this.userInfo.f_address, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '气   量：' + this.stairPrice.f_pregas + '方', fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '金   额：' + this.stairPrice.f_preamount + '元', fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '写卡日期：' + this.$PosUtil.getNowDate('yyyy-MM-dd HH:mm:ss'), fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '流 水 号：' + this.stairPrice.id, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '写卡结果：' + this.isWrited, fontsize: 2, isbold: true, align: 'left'})
      // printcontent.push({type: 3, text: '购气网点：' + this.stairPrice.f_operator, fontsize: 2, isbold: true, align: 'left'})
      printcontent.push({type: 3, text: '----------------------------', fontsize: 3, isbold: true, align: 'center'})
      // printcontent.push({type: 3, text: '写卡失败提示：请让POS机商户联系燃气公司工作人员进行处理！', fontsize: 2, isbold: true, align: 'left'})
      // 走纸
      printcontent.push({type: 4, value: 5, unit: 'pixel'})
      return this.$AndroidPlugin.print(printcontent)
    },
    outOfPage () {
      console.log('退出倒计时',this.countDown)
      if (this.countDown > 0) {
        --this.countDown
      } else {
        console.log('自动退出')
        window.clearInterval(InterValObj)
        this.show = false
        this.back('tomain')
      }
    }
  },
  ready () {
    console.log("进入到写卡界面")
    console.log(this.stairPrice)

    this.backData.f_user_id = this.userInfo.f_user_id
    this.backData.id = this.stairPrice.id
    this.backData.f_terminal_num = Vue.PosUtil.Sn
    this.writeRetryTime = this.$PosUtil.posParam.writeCardRetry
    this.Advert =  this.$PosUtil.posParam.Advert
      console.log(typeof this.Advert)
    if(typeof this.Advert!='undefined'){
        this.countDown = this.$PosUtil.posParam.Advert.outTime
        console.log("Advert",this.countDown)
        this.advertPath = require(`../../image/${this.$PosUtil.posParam.Advert.imgName}`)
    }
    this.writecard()
  }
}
</script>



